@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  font-family: 'Montserrat', Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: #2c3e50;
  background: #f8f9fa;
}

.hero {
  min-height: 60vh;
  background-size: cover;
  background-position: center;
  color: #fff;
  text-align: center;
  padding: 60px 20px 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.logo {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: #fff;
  object-fit: cover;
  margin-bottom: 18px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.15);
}
.hero h1 {
  font-size: 3rem;
  margin: 0 0 10px 0;
  letter-spacing: 2px;
}
.hero p {
  font-size: 1.3rem;
  margin-bottom: 22px;
}
.cta {
  display: inline-block;
  background: #e17055;
  color: #fff;
  padding: 14px 34px;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  transition: background 0.2s;
}
.cta:hover {
  background: #d35400;
}

/* Section Styles */
section {
  padding: 48px 18px;
  max-width: 900px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  margin-bottom: 26px;
  font-size: 2.1rem;
  color: #e17055;
}

/* About Section */
.about {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.03);
  margin-top: -40px;
}

/* Services */
.services {
  background: #f0f0f0;
  border-radius: 18px;
}
.service-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 28px;
}
.service-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.04);
  padding: 24px 22px;
  width: 210px;
  min-width: 170px;
  text-align: center;
  transition: transform 0.15s, box-shadow 0.15s;
}
.service-card:hover {
  transform: translateY(-7px) scale(1.03);
  box-shadow: 0 8px 32px rgba(44,62,80,0.10);
}
.service-card h3 {
  margin-top: 0;
  color: #0984e3;
}

.gallery-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 18px;
}
.gallery-img {
  width: 200px;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
}

/* Contact */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 18px;
  max-width: 370px;
  margin: 0 auto;
}
.contact-form input,
.contact-form textarea {
  padding: 14px;
  border-radius: 8px;
  border: 1px solid #bbb;
  font-family: inherit;
  font-size: 1.07rem;
  background: #f8f8f8;
}
.contact-form textarea {
  min-height: 70px;
  resize: vertical;
}
.contact-form button {
  background: #0984e3;
  color: #fff;
  padding: 12px 0;
  border: none;
  border-radius: 50px;
  font-weight: bold;
  font-size: 1.07rem;
  cursor: pointer;
  transition: background 0.2s;
}
.contact-form button:hover {
  background: #074f8a;
}

footer {
  text-align: center;
  padding: 20px 0;
  background: #222;
  color: #fff;
  margin-top: 40px;
  font-size: 1rem;
  letter-spacing: 1px;
}

@media (max-width: 700px) {
  .service-list {
    flex-direction: column;
    gap: 18px;
    align-items: center;
  }
  .gallery-img {
    width: 95vw;
    max-width: 320px;
    height: 120px;
  }
}